import { Component, OnInit } from '@angular/core';
import { NavController, AlertController, Alert, ShowWhen, App, Tabs } from 'ionic-angular';

import { AngularFireAuth } from 'angularfire2/auth';
import { AngularFirestore, AngularFirestoreCollection } from 'angularfire2/firestore';
import * as firebase from 'firebase/app';
import { enableLogging } from '@firebase/database-types';

import { ProfilePage } from '../profile/profile';
import { AddHousePage } from '../add-house/add-house';
import { HomePage } from '../home/home';

import { UserService } from '../shared-service/user.service';
import { QuerySnapshot } from '@firebase/firestore-types';
import { IfObservable } from 'rxjs/observable/IfObservable';
import { FirebaseStorage } from '@firebase/storage-types';

export interface House {
  id: string;
  name: string;
  description: string;
  province: string;
  city: string;
  age_from: number;
  age_to: number;
  garden: boolean;
  number_of_residents: number;
  only_female: boolean;
  only_male: boolean;
  smoker_allowed: boolean;
  price_month: number;
  residents: string;
  pets_allowed: boolean;
  images: object;
}

@Component({
  selector: 'page-profile-overview',
  templateUrl: 'profile-overview.html'
})
export class ProfileOverviewPage implements OnInit {


  constructor(public navCtrl: NavController, public afs: AngularFirestore, public afAuth: AngularFireAuth, public alertCtrl: AlertController, public userService: UserService,
    private app: App) {
    /* if (this.afAuth.authState) {
       this.showLogin = true;
       console.log('False: ' + this.showLogin);
       console.log('auth ' + this.afAuth.authState)
     } else {
       this.showLogin = false;
     }*/
    var doc: House;
    var db = firebase.firestore();

    this.afAuth.auth.onAuthStateChanged(
      (user) => {
        if (user) {
          this.userService.userEmail = user.email;
        } else {
          this.navCtrl.setRoot(ProfilePage);
        }
      });

  }

  ngOnInit() {

  }

  navigateHome() {
    const tabsNav = this.app.getNavByIdOrName('myNavigation') as Tabs;
    tabsNav.select(0);
    this.app.navPop();
  }

  add() {
    const tabsNav = this.app.getNavByIdOrName('myNavigation') as Tabs;
    tabsNav.select(2);
    this.app.navPop();

    //  this.navCtrl.setRoot(ProfilePage);
  }


  addCoHouse() {
    this.navCtrl.push(AddHousePage);
  }

  logout() {
    this.afAuth.auth.signOut().catch((error) => {
      // Handle Errors here.
      console.log("handle errors here");
      this.presentAlert("Afmelden niet gelukt.", 'Er ging iets mis bij het afmelden, probeer het later opnieuw.');
    });
  }

  alertDelete() {
    let alert = this.alertCtrl.create({
      title: 'Verwijder account',
      message: 'Als u uw account verwijderd worden ook alle woningen die u heeft toegevoegd verwijderd.',
      buttons: [
        {
          text: 'Annuleren',
          role: 'cancel',
          handler: () => {
            console.log('Cancel clicked');
          }
        },
        {
          text: 'Bevestigen',
          handler: () => {
            console.log('Buy clicked');
            var user = firebase.auth().currentUser;

            user.delete().then( () => {
              // User deleted.
              this.deleteAccount();
            }).catch( (error) => {
              this.presentAlert('Fout bij verwijderen','Oeps, er ging iets mis bij het verwijderen van uw account. Gelieve eerst af te melden en opnieuw aan te melden als dit probleem zich blijft voordoen.');
            });
           
          }
        }
      ]
    });
    alert.present();
  }

  async deleteAccount() {

    var db = firebase.firestore();

    var housesRef = db.collection('houses');
    var qry: firebase.firestore.QuerySnapshot = await housesRef.where('email', '==', this.afAuth.auth.currentUser.email).get();

    const batch = this.afs.firestore.batch();

    qry.forEach(doc => {
      batch.delete(doc.ref);
    });
    batch.commit();


    /* console.log('current user email: ' + this.afAuth.auth.currentUser.email);
     const qry: firebase.firestore.QuerySnapshot = await this.afs.collection('houses', ref => ref.where('email', '==', this.afAuth.auth.currentUser.email)).ref.get();
    
     const batch = this.afs.firestore.batch();
     qry.forEach(doc => {
       batch.delete(doc.ref);
     });
     batch.commit();*/
  }

  presentAlert(titleText, subtitleText) {
    let alert = this.alertCtrl.create({
      title: titleText,
      subTitle: subtitleText,
      buttons: ['Dismiss']
    });
    alert.present();
  }

}



