import { Component } from '@angular/core';
import { NavController, NavParams, ModalController, normalizeURL, App, Tabs } from 'ionic-angular';
import { DatePipe } from '@angular/common'

import { AngularFirestore, AngularFirestoreCollection } from 'angularfire2/firestore';
import { AngularFireAuth } from 'angularfire2/auth';
import { Observable } from 'rxjs/Observable';

import { HouseDetailPage } from '../houseDetail/houseDetail';
import { ProfilePage } from '../profile/profile';

export interface Province { id: string; name: string; }
export interface House {
  id: string;
  name: string;
  description: string;
  province: string;
  city: string;
  age_from: number;
  age_to: number;
  garden: boolean;
  number_of_residents: number;
  only_female: boolean;
  only_male: boolean;
  smoker_allowed: boolean;
  price_month: number;
  residents: string;
  pets_allowed: boolean;
  images: object;
}

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {
  private itemsCollectionProvince: AngularFirestoreCollection<Province>;
  private itemsCollectionHouse: AngularFirestoreCollection<House>;

  provinces: Observable<Province[]>;
  houses: Observable<House[]>;

  imagePath;
  current_date;
  images;


  constructor(public navCtrl: NavController, private readonly afs: AngularFirestore, public afAuth: AngularFireAuth, private app: App, private datePipe: DatePipe) {
    this.itemsCollectionProvince = afs.collection<Province>('provinces', ref => ref.where('country', '==', 'BE').orderBy('name_nl'));
    // .valueChanges() is simple. It just returns the 
    // JSON data without metadata. If you need the 
    // doc.id() in the value you must persist it your self
    // or use .snapshotChanges() instead. See the addItem()
    // method below for how to persist the id with
    // valueChanges()
    this.provinces = this.itemsCollectionProvince.valueChanges();

    this.current_date = new Date();
    let currentDateFormatted = this.datePipe.transform(this.current_date, 'yyyy-MM-dd');

    this.itemsCollectionHouse = afs.collection<House>('houses', ref => ref.where('valid_until', '>', currentDateFormatted));
    this.houses = this.itemsCollectionHouse.valueChanges();
    /*this.itemsCollectionHouse.valueChanges().subscribe(houseSub => {
      for (let house of houseSub) {
        console.log("house images: " + house.images);
        this.images = house.images;
        console.log("this images: " + this.images);

        /* if(this.images.length > 0) {
           for(var i = 0; i <= this.images.length; i++){
         //      this.imagePath.add(houseSub.images.image[i]);
           }
       }
      }
    });*/


    /*
    .valueChanges().subscribe(discountCode => {
         this.discountCodes = discountCode;
         console.log(this.discountCodes);
 
         if (this.discountCodes.length > 0) {
           console.log("In if loop");
           for (let code of this.discountCodes) {
             console.log("for loop " + code.code);
             this.discountCodeOK = 'Geldige kortingscode';
             this.discountCodeNotOK = '';
             this.price = "0,00";
           }
         } else {
           console.log("No discount code found");
           this.discountCodeNotOK = 'Ongeldige kortingscode';
           this.price = '9,99';
           this.discountCodeOK = '';
         }
       }); 
     */

    //test to load image on ios device (broken after using uiwebview)
    var testImage = "assets/imgs/house_test_1.jpeg";
    this.imagePath = normalizeURL(testImage);



  }

  navigateHome() {
    const tabsNav = this.app.getNavByIdOrName('myNavigation') as Tabs;
    tabsNav.select(0);
    this.app.navPop();
  }
  /*addItem(name: string) {
    // Persist a document id
    const id = this.afs.createId();
    const province: Province = { id, name };
    this.itemsCollection.doc(id).set(province);
  }*/

  navigateToHouseDetail(house) {
    console.log("Navigate to next page");
    this.navCtrl.push(HouseDetailPage, {
      house: house
    });
  }

  add() {
    const tabsNav = this.app.getNavByIdOrName('myNavigation') as Tabs;
    tabsNav.select(2);
    this.app.navPop();

    //  this.navCtrl.setRoot(ProfilePage);
  }

}



