import { Component } from '@angular/core';
import { NavController, NavParams, App, Tabs } from 'ionic-angular';
import { HomePage} from '../home/home';

//import { AngularFirestore, AngularFirestoreCollection } from 'angularfire2/firestore';
//import { Observable } from 'rxjs/Observable';

@Component({
    selector: 'page-house-detail',
    templateUrl: 'houseDetail.html'
  })
  export class HouseDetailPage {
    public house: any = [];
    constructor(public navCtrl: NavController, public navParam: NavParams, private app: App) {
        this.house = navParam.get('house');
       // this.imageList.push(this.business.images);
       }

       navigateHome(){
        const tabsNav = this.app.getNavByIdOrName('myNavigation') as Tabs;
        tabsNav.select(0);
        this.app.navPop();
      }
    
      add(){
        const tabsNav = this.app.getNavByIdOrName('myNavigation') as Tabs;
        tabsNav.select(2);
        this.app.navPop();
    
      //  this.navCtrl.setRoot(ProfilePage);
      }
    

  }


